input {
  border: 0px solid #000;
  margin: 0;
  background: transparent;
  width: 100%;
}

/*table tr td {
  border-right: 1px solid #000;
  border-bottom: 1px solid #000;
}*/

table {
  background: #fff none repeat scroll 0 0;
}
.bill-table thead {
  border-bottom: 2px solid #ccc;
}
table tr:nth-child(even) {
  background: #ccc;
}

table tr:nth-child(odd) {
  background: #eee;
}

.invoice-table tr:nth-child(even) {
  background: #eee;
}

.invoice-table tr:nth-child(odd) {
  background: #eee;
}

table tr {
  background: #eee;
}
table td {
  min-width: 80px;
}

table thead th {
  font-weight: bold;
  min-width: 80px;
  text-align: left;
}
.bill-table table thead th {
  font-weight: bold;
  min-width: 80px;
  text-align: center;
}

.table-summary {
  text-align: right;
  font-weight: bold;
}
