/* Works on Firefox */
.scroller {
  scrollbar-color: rgba(138, 135, 135, 0.5) white;
  scrollbar-width: thin;
}

/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 3px;
}

*::-webkit-scrollbar-track {
  background: white;
}

*::-webkit-scrollbar-thumb {
  background-color: rgba(138, 135, 135, 0.5);
}
